.cont{
    color: #fff;
    text-align: left;
    flex-wrap: wrap;
    word-wrap: break-word;
    padding-left: 8px;
    
    @media screen and(max-width:340px) {
        padding-left: 6px;
        
    }
    .wrapper{
        background-color: #141228;
        border: none;
        border-radius: 10px;
        width: 96%;
        margin: auto;
        max-width: 1700px;
        padding: 4px 0px 15px 9px;


    }
    .overflow{
        display: inline-block;
        overflow-x: auto;
        word-wrap: normal;

        &::-webkit-scrollbar {
            cursor: pointer;
            width: 0.51em;
          }
           
          &::-webkit-scrollbar-track {
            box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
          }
           
          &::-webkit-scrollbar-thumb {
            background-color: rgba(169, 169, 169, 0.119);
            outline: 1px solid rgba(112, 128, 144, 0.044);
            border-radius: 10px;
          }
          &::-webkit-scrollbar-thumb :hover{
            background-color: rgba(169, 169, 169, 0.194);
          }
    }


    
    h2{
        border-bottom: 1px solid rgba(255, 255, 255, 0.16);
        font-size: 2.5rem;
        font-weight: 100;
        max-width: 500px;
    }

    h3{
        text-align: left;
        font-size: 1.9rem;
        font-weight: 200;
    }
    h4{
        text-align: left;
        font-weight: 200;
    }

    li{
        font-size: 1rem;
        font-weight: 100;
        margin-bottom: 5px;
        
        @media screen and (max-width:300px) {
            font-size: 0.97rem;
            margin-bottom: 7px;
        }
        
    }
    
    p{
        font-size: 1rem;
        font-weight: 100;
        width: fit-content;

        @media screen and (max-width:300px) {
            font-size: 0.97rem;
        }
    }
    .datab{
        width: max-content;
    }
    .name{
        color: rgb(216, 217, 255);
        font-size: 1.2rem;
        margin: 0.41rem 0;
    }
    .types{
        color: rgb(70, 205, 242);
        font-size: 1.2rem;
        margin: 0.41rem 8px;

    }
    .importantparam{
        color: rgb(249, 75, 122);
        font-size: 1.2rem;
        margin: 0.41rem 0;
    }
}


.spanbox{
    border: none;
    padding: 0.5% 2%;
    border-radius: 6px;
    font-size: 1.3rem;
    color: #fff;
}
.get{
    background-color: #0bf254;
}
.post{
    background-color: #2a0bf2;
}
.patch{
    background-color: #0badf2;
}
.delete{
    background-color: #f20b36;
}
