.cont {
  color: #fff;
  text-align: left;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  word-wrap: break-word;
  padding-left: 8px;
}

@media screen and (max-width: 340px) {
  .cont {
    padding-left: 6px;
  }
}

.cont .wrapper {
  background-color: #141228;
  border: none;
  border-radius: 10px;
  width: 96%;
  margin: auto;
  max-width: 1700px;
  padding: 4px 0px 15px 9px;
}

.cont .overflow {
  display: inline-block;
  overflow-x: auto;
  word-wrap: normal;
}

.cont .overflow::-webkit-scrollbar {
  cursor: pointer;
  width: 0.51em;
}

.cont .overflow::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
          box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

.cont .overflow::-webkit-scrollbar-thumb {
  background-color: rgba(169, 169, 169, 0.119);
  outline: 1px solid rgba(112, 128, 144, 0.044);
  border-radius: 10px;
}

.cont .overflow::-webkit-scrollbar-thumb :hover {
  background-color: rgba(169, 169, 169, 0.194);
}

.cont h2 {
  border-bottom: 1px solid rgba(255, 255, 255, 0.16);
  font-size: 2.5rem;
  font-weight: 100;
  max-width: 500px;
}

.cont h3 {
  text-align: left;
  font-size: 1.9rem;
  font-weight: 200;
}

.cont h4 {
  text-align: left;
  font-weight: 200;
}

.cont li {
  font-size: 1rem;
  font-weight: 100;
  margin-bottom: 5px;
}

@media screen and (max-width: 300px) {
  .cont li {
    font-size: 0.97rem;
    margin-bottom: 7px;
  }
}

.cont p {
  font-size: 1rem;
  font-weight: 100;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

@media screen and (max-width: 300px) {
  .cont p {
    font-size: 0.97rem;
  }
}

.cont .datab {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}

.cont .name {
  color: #d8d9ff;
  font-size: 1.2rem;
  margin: 0.41rem 0;
}

.cont .types {
  color: #46cdf2;
  font-size: 1.2rem;
  margin: 0.41rem 8px;
}

.cont .importantparam {
  color: #f94b7a;
  font-size: 1.2rem;
  margin: 0.41rem 0;
}

.spanbox {
  border: none;
  padding: 0.5% 2%;
  border-radius: 6px;
  font-size: 1.3rem;
  color: #fff;
}

.get {
  background-color: #0bf254;
}

.post {
  background-color: #2a0bf2;
}

.patch {
  background-color: #0badf2;
}

.delete {
  background-color: #f20b36;
}
